
import * as React from 'react';
import { RenderNodeProps } from '@ali/4ever-cangjie';
import { PlaceholderWrapper } from '@ali/4ever-component';

interface InvalidProps {
  attributes: RenderNodeProps['attributes'];
}

const Invalid: React.FC<InvalidProps> = (
  props,
) => {
  const { attributes, children } = props;
  return (
    <PlaceholderWrapper data-testid="refblock-invalid" {...attributes}>
      {children}
    </PlaceholderWrapper>
  );
};

export default Invalid;
